import GAListener from 'components/GAListener';
import { MainLayout } from 'components/Layout';
import PageSpinner from 'components/PageSpinner';
import React from 'react';
import componentQueries from 'react-component-queries';
import { BrowserRouter, Redirect, Route, Switch } from 'react-router-dom';
import './styles/reduction.scss';

const ApiTokenPage = React.lazy(() => import('pages/ApiTokenPage'));
const SignUpPage = React.lazy(() => import('pages/SignUpPage'));
const SwaggerPage = React.lazy(() => import('pages/SwaggerPage'));
const TemplatePage = React.lazy(() => import('pages/TemplatePage'));
const PrivatePage = React.lazy(() => import('pages/PrivatePage'));
const PublicPage = React.lazy(() => import('pages/PublicPage'));
const WalletPage = React.lazy(() => import('pages/WalletPage'));
const EtcPage = React.lazy(() => import('pages/EtcPage'));
const AuthPage = React.lazy(() => import('pages/AuthPage'));

const getBasename = () => {
  return `/${process.env.PUBLIC_URL.split('/').pop()}`;
};

class App extends React.Component {
  render() {
    return (
      <BrowserRouter basename={getBasename()}>
        <GAListener>
          <Switch>
            <MainLayout breakpoint={this.props.breakpoint}>
              <React.Suspense fallback={<PageSpinner />}>
                <Route exact path="/" component={SignUpPage} />
                <Route exact path="/signup2" component={SignUpPage} />
                <Route exact path="/apitoken" component={ApiTokenPage} />
                <Route exact path="/swagger" component={SwaggerPage} />
                <Route exact path="/template" component={TemplatePage} />
                <Route exact path="/private" component={PrivatePage} />
                <Route exact path="/public" component={PublicPage} />
                <Route exact path="/wallet" component={WalletPage} />
                <Route exact path="/etc" component={EtcPage} />
                <Route exact path="/auth" component={AuthPage} />
              </React.Suspense>
            </MainLayout>
            <Redirect to="/" />
          </Switch>
        </GAListener>
      </BrowserRouter>
    );
  }
}

const query = ({ width }) => {
  if (width < 575) {
    return { breakpoint: 'xs' };
  }

  if (576 < width && width < 767) {
    return { breakpoint: 'sm' };
  }

  if (768 < width && width < 991) {
    return { breakpoint: 'md' };
  }

  if (992 < width && width < 1199) {
    return { breakpoint: 'lg' };
  }

  if (width > 1200) {
    return { breakpoint: 'xl' };
  }

  return { breakpoint: 'xs' };
};

export default componentQueries(query)(App);
